import "./styles.css";
import React, { useState } from "react";
import Eventchange from "./Components/Eventchange";
import Counter from "./Components/Counter";
export default function App() {
  const [active, setActive] = useState("Counter");
  return (
    <div>
      <nav>
        <button className="buttonnav" onClick={() => setActive("Counter")}>
          Counter
        </button>
        <button className="buttonnav" onClick={() => setActive("Eventchange")}>
          EventChange
        </button>
      </nav>
      <div className="display">
        {active === "Counter" && <Counter />}
        {active === "Eventchange" && <Eventchange />}
      </div>
    </div>
  );
}
