import React from "react";

class Eventchange extends React.Component {
  constructor() {
    super();
    this.state = { data: "akshay" };
  }
  click() {
    this.setState({ data: "shetty" });
  }
  render() {
    return (
      <>
        <h1>{this.state.data}</h1>
        <button className="buttoninside" onClick={() => this.click()}>
          click
        </button>
      </>
    );
  }
}
export default Eventchange;
